                             </el-header>
                    <el-main>
                        <!--工具条-->
                    <el-row>
                    <div v-show = "file_table==='false'"
                         v-loading="loadworkspace"
                     element-loading-text="拼命加载中"
                     element-loading-spinner="el-icon-loading"
                     element-loading-background="rgba(0, 0, 0, 0.8)" style="margin-top: 30px;height:750px" align="center">
                    <iframe    
                            id="iframeContain"
