                                style="width: 100%" v-loading="listLoading">
                          <el-table-column type="index" label="#" min-width="10%">
                          </el-table-column>
                          <el-table-column prop="name" label="文件名称" min-width="18%">
                              <template slot-scope="scope">
                                <!--<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">移除</el-button>-->
                                <el-button icon="fa fa-folder-o"
                                           v-if="scope.row.is_dir===true"
