                                           @click="handleFileDir(scope.$index, scope.row)"
                                           size="big">&nbsp;{{scope.row.name}}/
                                </el-button>
                                <el-button icon="fa fa-file-o"
                                           v-if="scope.row.is_dir===false"
                                           type="text"
