                        <el-table-column prop="last_modified"   label="上次修改时间" min-width="10%">
                            <template slot-scope="scope">
                                <timeago :datetime="scope.row.last_modified" locale="zh-CN"></timeago>
                            </template>
                        </el-table-column>
                        <el-table-column prop="description" label="" style="float:right" min-width="10%">
                            <template slot-scope="scope">
                                <el-dropdown trigger="hover" class="editGroup" style="margin-right:1%">
                                    <i class="el-icon-more"></i>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item @click.native="handleDelFileDir(scope.row.is_dir,scope.row.name)">删除</el-dropdown-item>
                                        <el-dropdown-item @click.native="handleEditFileName(scope.row.is_dir,scope.row.name)">重命名</el-dropdown-item>
                                        <el-dropdown-item  v-if="scope.row.is_dir===false">
                                            <router-link :to="{ name: '查看文件', params: {filepath:directory,fileToView: scope.row.name}}" style='text-decoration: none;'>
                                            查看
                                           </router-link>
