        </el-row>
</el-main>
</el-container>
    </el-card>
</template>
      </el-tab-pane>
        </el-tabs >
        </el-collapse-item>
       </el-collapse>
      <el-dialog
      title="Stage Transition"
      :visible.sync="StageTranVisible"
      width="30%">
        <span>Transition to &nbsp;&nbsp;&nbsp;&nbsp;<i class="el-icon-right"></i>&nbsp;&nbsp;&nbsp;&nbsp;<el-tag size="small" :type="stage_status0">
            {{status_msg}}</el-tag></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="StageTranVisible = false">取 消</el-button>
          <el-button type="primary" @click="StageTranSubmit">确 定</el-button>
        </span>
    </el-dialog>

    <!--删除文件-->
    <el-dialog :title="delTitle"
               width="30%"
               :visible.sync="delFileDirVisible"
               :close-on-click-modal="false">
        <el-divider></el-divider>
        <div style="font-size: 15px">
            <el-row :gutter="20" style="margin: 10px">
                <span>{{tobeDelFileDir}}</span>
            </el-row>
        </div>
        <el-divider></el-divider>
        <div slot="footer" class="dialog-footer">
            <el-button @click.native="delFileDirVisible = false">取消</el-button>
            <el-button type="primary" @click.native="delFileDirSubmit" :loading="delFileDirLoading">提交</el-button>
        </div>
      </el-dialog>
      <!--编辑文件名称-->
      <el-dialog :title="rename_tile_type"
                 :visible.sync="editFileNameFormVisible"
                 :close-on-click-modal="false" style="width: 60%; left: 20%">
          <el-alert style="margin-bottom:20px;"
                    title="重要提示"
                    type="warning"
                    closable=flase
                    description="重命名文件将切断与其版本历史的联系--版本历史将与旧文件名一起保存，您将无法比较修订"
                    show-icon>
          </el-alert>
          <el-form :model="editFileNameForm" label-width="80px"  :rules="editFileNameFormRules" ref="editFileNameForm">
              <el-form-item label="新名称" prop='newName'>
                  <el-input v-model.trim="editFileNameForm.newName" auto-complete="off"></el-input>
              </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
              <el-button @click.native="editFileNameFormVisible = false">取消</el-button>
              <el-button type="primary" @click.native="editFileNameSubmit" :loading="editFileNameLoading">提交</el-button>
          </div>
        </el-dialog>

  </div>
</template>

<script>

import { test, getModelVersionBaseinfo, getModelVersions,getModelRun} from '@/api/api'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'

export default {
    data () {
        return {
            file_table:'true',
            toolbar:'toolbar',
            cur_path : '',
            editFileNameLoading: false,
            editFileNameFormVisible: false, // 编辑界面是否显示
            editFileNameFormRules: {
                newName: [
                    { required: true, message: '请输入名称', trigger: 'blur' }
                    // { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
                ]
            },
            // 编辑界面数据
            editFileNameForm: {
                newName: ''
            },
            rename_tile_type: '',
            model_files:[],
            file_content:'',
            loadworkspace:false,
            status_msg:'',
            StageTranVisible:false,
            to_be_stage2:'None',
            to_be_stage3:'None',
            to_be_stage4:'None',
            stage_status1:'info',
            stage_status2:'info',
            stage_status3:'info',
            stage_status4:'info',
            selsVersionCompare: [],
            versionList: [],
            stageStatus: 'All',
            editLoading:false,
            ModelVersionbaseInfo:{},
            editModelVersionVisible: false,
            activeNames: ['1'],
            sels:[],
            total: 0,
            page: 1,
            listLoading: false,
            editModelForm:{
                description: ''
            }
        }
    },
  created () {
    // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
    // this.form.Tasks = this.Tasks[0].id
  },
    filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
  methods: {
      _back () {
          let self = this
          this.cur_path = this.back
          this.getFileDir()
        },
      handleFileDir: function (index, row) {
          let self = this
          if (row.is_dir) {
              this.cur_path = row.rel_path
              console.log(this.cur_path,"this.path")
              self.getFileDir()
          } else {
              this.file_table='false'
              /*this.$router.push(
                      {
                          name: '查看文件',
                          params:
                          {
                              model_name: this.$route.params.model_name,
                              version_id: this.$route.params.version_id,
                              filepath:this.directory,
                              fileToView: row.name
                          }
                      })*/
          }
          console.log(this.cur_path, this.back_display, 'this.path')
      },
      editFileNameSubmit () {
            this.$refs.editFileNameForm.validate((valid) => {
                if (valid) {
                    let self = this
                    this.$confirm('确认提交吗？', '提示', {}).then(() => {
                        self.editFileNameLoading = true
                        $.ajax({
                            type: 'post',
                            url: test + '/api/modelfile/rename_file_dir_name',
                            async: true,
                            data: JSON.stringify(
                                {
                                    name: this.editFileNameForm.newName,
                                    old_name: this.oldName,
                                    path: this.directory
                                }
                            ),
                            headers: {
                                'Content-Type': 'application/json',
                                Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
                            },
                            timeout: 5000,
                            success: function (data) {
                                self.editFileNameLoading = false
                                if (data.code === '999999') {
                                    self.editFileNameFormVisible = false
                                    self.$message({
                                        message: '修改成功',
                                        center: true,
                                        type: 'success'
                                    })
                                    self.getFileDir()
                                } else {
                                    self.editFileNameFormVisible = false
                                    self.$message.error({
                                        message: data.msg,
                                        center: true
                                    })
                                }
                            }
                        })
                    })
                }
            })
        },
      // 修改文件弹窗显示
        // eslint-disable-next-line camelcase
        handleEditFileName (is_dir_rename, name) {
            // eslint-disable-next-line camelcase
            if (is_dir_rename) {
                this.rename_tile_type = '重命名目录:' + name
            } else {
                this.rename_tile_type = '重命名文件:' + name
            }
            this.oldName = name
            this.editFileNameFormVisible = true
            this.editFileNameForm.newName = name
        },
      handleDelFileDir (is_dir_del, name) {
            this.delFileDirVisible = true
            this.delName = name
            // eslint-disable-next-line camelcase
            if (is_dir_del) {
                this.tobeDelFileDirType = 'dir'
                this.delTitle = '删除目录'
                this.tobeDelFileDir = '您确定要删除目录：' + name + '？'
            } else {
                this.tobeDelFileDirType = 'file'
                this.delTitle = '删除文件'
                this.tobeDelFileDir = '您确定要删除文件：' + name + '？'
            }
        },
      // 获取项目动态
        getFileDir () {
            this.listLoading = true
            let self = this
            $.ajax({
                type: 'get',
                url: test + '/api/models/get_model_files',
                async: true,
                data: {page: Number(self.page),
                       model_name: this.$route.params.model_name,
                       version_id: this.$route.params.version_id,
                       path: this.cur_path },
                headers: {
                    // 'Content-Type': 'application/json',
                    Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
                },
                timeout: 5000,
                success: function (data) {
                    self.listLoading = false
                    if (data.code === '999999') {
                      self.total = data.data.total
                      self.is_subdirectory = data.data.is_subdirectory
                      self.directory = data.data.directory
                      self.back = data.data.back
                      self._code = data.data.code
                      self.model_files = data.data.files
                      // use to upload data
                      self.pathParam = { pathParam: self.directory }
                      if (self.is_subdirectory) {
                          self.back_display = 'float:right'
                          self.toolbar='toolbar'
                      } else {
                          self.back_display = 'display:none'
                          self.toolbar=''
                      }
                      console.log(data, 'dataffffff')
                  } else {
                      self.$message.error({
                          message: "data.msg",
                          center: true
                      })
                  }
              }
          })
      },
      getJupyterNotebook () {
          let self = this
          this.loadworkspace= true
          let param = JSON.stringify({ model_name: this.$route.params.model_name,
                                      version_id: this.$route.params.version_id })
          $.ajax({
              type: 'post',
              url: test + '/api/models/get_models_markdown',
              async: true,
              data: param,
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: (data) => {
                  if(data.code==="999999"){
                      self.file_content = data.data
                  }else{
                       self.file_content = data
                  }
                 
                  this.loadworkspace= false
                  console.log(self.file_content,"file_content")
              }
          })
      },
      StageTranSubmit () {
          let self = this
          let param = JSON.stringify({ model_name: this.$route.params.model_name,
                                      version_id: this.$route.params.version_id,
                                      stage: this.status_msg,
                                      user_key:JSON.parse(sessionStorage.getItem('name'))
                                     })
          console.log(param, 'dsdds')
          $.ajax({
              type: 'post',
              url: test + '/api/models/model_stage_transform',
              async: true,
              data: param,
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: (data) => {
                  self._getModelVersionBaseinfo()
                  this.StageTranVisible=false
              }
          })
      },
      handleModelStage(command) {
          this.StageTranVisible=true
          if(command==='a'){
              this.status_msg = this.to_be_stage2
          }else if(command==='b'){
              this.status_msg = this.to_be_stage3
          }else{
              this.status_msg = this.to_be_stage4
          }
          if(this.status_msg==='Production'){
              this.stage_status0='success'
          }else if(this.status_msg==='Staging'){
              this.stage_status0='warning'
          }else{
              this.stage_status0='info'
          }
      },

      linktomodelrun: function(){
          this.listLoading = true
          let self = this
          let params = {
              run_id: this.ModelVersionbaseInfo.run_id,
              user_key: JSON.parse(sessionStorage.getItem('name')),
          }
          let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          }
          getModelRun(headers, params).then(_data => {
              let { msg, code, data } = _data
              self.listLoading = false
              if (code === '999999') {
                  self.experiment_id = data.info.experiment_id
                  self.run_id = data.info.run_id
                  this.$router.push(
                      {
                          name: '模型实验详情',
                          params:
                          {
                              experiment_id: self.experiment_id,
                              run_id: self.run_id
                          }
                      })
                  
       
              } else {
                  self.$message.error({
                      message: msg,
                      center: true
                  })
              }
          })
         //console.log(this.ModelVersionbaseInfo.run_id,"run_idd") 
          
      },
      // 显示编辑界面
      handleModelVersionDescEdit: function (index, row) {
          this.editModelVersionVisible = true
          let desc = this.ModelVersionbaseInfo.description
          // this.editForm = Object.assign({}, row);
          this.editModelForm = Object.assign({}, { description: desc })
    },
      disabledCheckBox(row, index) {
          if (row.is_dir===false) {
              return true
          } else {
              return false
          }
      },

      handleModelOps(command) {
          console.log(command,"command")
           this.$router.push(
               {
                   name: '模型即服务',
                   params:
                   {
                       project_id: this.$route.params.project_id,
                       task_id: this.$route.params.task_id,
                       model_id: command
                   }
               })
          this.$message('click on item ' + command);
          //"handleModelOps(scope.$index,scope.row)"
      },
    // 获取模型任务列表
    _getModelVersionBaseinfo () {
      this.listLoading = true
      let self = this
      let params = {
        name: this.$route.params.model_name,
        version: this.$route.params.version_id,
        user_key: JSON.parse(sessionStorage.getItem('name'))
      }

      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelVersionBaseinfo(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.listLoading = false
        if (code === '999999') {
            self.ModelVersionbaseInfo = data
            if (self.ModelVersionbaseInfo.current_stage==='Production'){
                self.stage_status1="success"
                self.to_be_stage2 = "Staging"
                self.stage_status2 = "warning"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Archived"
                self.stage_status4 = "info"
            }else if(self.ModelVersionbaseInfo.current_stage==='Staging'){
                self.stage_status1="warning"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Archived"
                self.stage_status4 = "info"
            }else if(self.ModelVersionbaseInfo.current_stage==='Archived') {
                self.stage_status1="info"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "None"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Staging"
                self.stage_status4 = "warning"                
            }else{
                self.stage_status1="info"
                self.to_be_stage2 = "Production"
                self.stage_status2 = "success"
                self.to_be_stage3 = "Archived"
                self.stage_status3 = "info"
                self.to_be_stage4 = "Staging"
                self.stage_status4 = "warning"  
            }
            console.log(self.ModelVersionbaseInfo,"ModelbaseInfo")
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    editSubmit: function(){
        let self = this
        this.editLoading= true
        let param = JSON.stringify({ 
            model_name: this.$route.params.model_name,
            version: this.$route.params.version_id,
            description: this.editModelForm.description,
            user_key: JSON.parse(sessionStorage.getItem('name'))
        })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'POST',
        url: test + '/api/models/edit_model_version_description',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            // console.log(data,"dddddd")
            this.editLoading= false
            this.editModelVersionVisible = false
            this._getModelVersionBaseinfo()
            
        }
      })
        
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },

    handleCurrentChange (val) {
        this.page = val
        this.getFileDir()
    },
    selsChangeVersionTracking: function (sels) {
      this.selsVersionCompare = sels
    },
  },
  mounted () {
      this._getModelVersionBaseinfo()
      this.getJupyterNotebook()
      this.getFileDir()
      
          
  }
}
</script>
